body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100vh;
  width: 100vw;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#6f42c1+0,6610f2+100 */
  background: rgb(111,66,193); /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(111,66,193,1) 0%,rgba(102,16,242,1) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(111,66,193,1) 0%,rgba(102,16,242,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#6f42c1', endColorstr='#6610f2',GradientType=0 ); /* IE6-9 */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

input[type="text"],
input[type="button"] {
  -webkit-appearance: none;
  border-radius: 0;
}

* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}
.App {
  padding: 3px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: 16px;
  margin-top: 40px;
  width: 100%;
  max-width: 600px;
  color: #0f0621;
}

button {
  cursor: pointer;
  font-size: inherit;
  font-weight: bold;
}

#active-remaining-todos {
  margin-bottom: 50px;
  text-align: right;
  font-size: 1.5em;
}

#delete-all-completed {
  margin-bottom: -20px;
  -webkit-align-self: flex-end;
          align-self: flex-end;
  padding: 12px 16px;
  border: none;
  background: #790201;
  color: #bdafd9;
}

@media screen and (max-width: 768px) {
  .App {
    width: 96%;
  }
}
#add-todo {
  display: -webkit-flex;
  display: flex;
}

#add-todo-form {
  width: 100%;
  display: -webkit-flex;
  display: flex;
}

#add-todo-form input {
  -webkit-flex: 8 1;
          flex: 8 1;
  font-size: inherit;
  padding: 8px;
  border: none;
  width: 100%;
  height: 100%;
}

#add-todo-form button {
  -webkit-flex: 1 1;
          flex: 1 1;
  border: none;
  background-color: #2f1469;
  color: white;
  height: 100%;
  width: 100%;
}

.error-msg {
  color : #790201;
  background: rgba(205, 182, 255, 0.2);
  padding: 10px 0;
  font-weight: bold;
}
#ratings {
  display: -webkit-flex;
  display: flex;
  padding: 0 10px;
  background: white;

}
#ratings .rating {
  border: none;
  font-weight: bold;
  font-size: 1.3em;
  color: #bdafd9;
  background: none;
}

#rating-1 { -webkit-order: -5; order: -5 }
#rating-2 { -webkit-order: -4; order: -4 }
#rating-3 { -webkit-order: -3; order: -3 }
#rating-4 { -webkit-order: -2; order: -2 }
#rating-5 { -webkit-order: -1; order: -1 }

#ratings .rating:hover,
#ratings .rating:hover ~ .rating {
  color: #2f1469;
}

#ratings .rated {
  color: #2f1469;
}

span {
  cursor: pointer;
  -webkit-align-self: center;
          align-self: center;
  margin: 0 3px;
}
.todo {
  display: -webkit-flex;
  display: flex;
  border-bottom: 1px solid #3d1a88;
  overflow: hidden;
}

.todo-txt {
  -webkit-flex: 4 1;
          flex: 4 1;
  text-align: left;
  padding-left: 5px;
}

.delete-todo-btn {
  background: #790201;
  color: #bdafd9;
  border: none;
  display: none;
}

@-webkit-keyframes slide-in {
 from {
   width: 0;
 }
 to {
   width: 60px;
 }
}

@keyframes slide-in {
 from {
   width: 0;
 }
 to {
   width: 60px;
 }
}

.todo:hover .delete-todo-btn {
  display: block;
  -webkit-animation-name: slide-in;
          animation-name: slide-in;
  -webkit-animation-duration: 0.1s;
          animation-duration: 0.1s;
}

.completed-btn {
  -webkit-align-self: center;
          align-self: center;
  margin-left: 8px;
  font-size: inherit;
}

.urgency-rating {
  font-size: 1.2em;
  color: #2f1469;
  font-weight: bold;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin: 0 10px;
}
p { text-align: center; }

.todo-list {
  -webkit-flex: 1 1;
          flex: 1 1;
  background-color: rgba(205, 182, 255, 0.2);
  /* padding: 0 20px; */
  /* width: 100%; */
  margin: 40px 0;
}

.completed-item .todo-txt {
  text-decoration: line-through;
  color: #bdafd9
}

